import { Container, Alert } from "react-bootstrap"
import { Link } from "react-router-dom"
import { Helmet } from "react-helmet"
import { FC, useState } from "react"

import AnonymusAdsBanner from "../../components/anonymus-ads-banner"
import AdsterraBanner from "../../components/adsterra-banner"
import VideoKeywords from "../../components/video-keywords"
import AdCashBanner from "../../components/adcash-banner"
import Footer from "../../components/footer"
import Header from "../../components/header"

import videoTutorialAmicoImage from "../../assets/images/ilustrations/Video tutorial-amico.svg"
import videoUploadAmicoImage from "../../assets/images/ilustrations/Video upload-amico.svg"
import seoAnalyticsTeamAmicoImage from "../../assets/images/ilustrations/SEO analytics team-amico.svg"
import webSearchAmicoImage from "../../assets/images/ilustrations/Web search-amico.svg"

import "./style.css"

function isValidUrl(url: string): boolean {
    const patten = /^(http[s]?:\/\/)?(www\.)?(youtube\.com|youtu\.be)/
    return patten.test(url)
}

const Home: FC = () => {
    const [ alert, setAlert ] = useState<any>()
    const [ videoKeywordsElement, setVideoKeywordsElement ] = useState<any>()

    function toggleScrollToTopButton() {
        const $btnScrollToTop = document.getElementById("btnScrollToTop")

        if ($btnScrollToTop) {
            if (document.documentElement.scrollTop > 300) {
                $btnScrollToTop.style.visibility = "visible"
                $btnScrollToTop.style.opacity = "1"
            } else {
                $btnScrollToTop.style.visibility = "hidden"
                $btnScrollToTop.style.opacity = "0"
            }
        }
    }
      
    function scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        })
    }
      
    window.onscroll = () => toggleScrollToTopButton()

    function handleGetInfo(event: any) {
        setVideoKeywordsElement(null)

        const $videoUrlInput = document.querySelector("#video-url") as any
        const videoUrl = $videoUrlInput.value as string

        if (!isValidUrl(videoUrl)) {
            setTimeout(setAlert, 1500)

            return setAlert(
                <Alert variant="danger">Insira uma URL válida!</Alert>
            )
        }

        $videoUrlInput.disabled = true

        setAlert(
            <Alert variant="primary">Aguarde estamos buscando as tags do vídeo...</Alert>
        )

        fetch(`https://marcuth-utils-api.marcuth.repl.co/scraping/youtube/video/details/?url=${videoUrl}`)
            .then(response => response.json())
            .then(videoInfo => {
                setVideoKeywordsElement(
                    <VideoKeywords keywords={videoInfo.keywords}/>
                )

                $videoUrlInput.disabled = false

                setAlert(
                    <Alert variant="success">As tags foram obtidas com sucesso!</Alert>
                )

                setTimeout(() => setAlert(null), 1500)
            })
            .catch(error => {
                setTimeout(setAlert, 1500)

                return setAlert(
                    <Alert variant="danger">{error.message}</Alert>
                )
            })
    }

    return (
        <>
            <Helmet>
                <title>yTags - Busque Tags e Palavras-Chave de Vídeos do YouTube | Home</title>
            </Helmet>
            <Header/>
            <Container>
                <h1 className="main-title animate-load-from-top">Seja bem-vindo ao <strong>yTags! </strong></h1>
                <div className="url-form animate-load-from-left">
                    <h2 className="secondary-title mb-3">Pegue tags de qualquer vídeo do YouTube e melhore o SEO do seu Canal!</h2>
                    <div className="fields">
                        <input id="video-url" placeholder="https://youtu.be/5u_-eo1bXec" type="url"/>
                        <button className="btn-send" title="Pegar tags do vídeo" onClick={handleGetInfo}>
                            <i className="bi bi-send"></i>
                        </button>
                    </div>
                    <div className="mt-3">{alert}</div>
                </div>
                {videoKeywordsElement}
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anúncio</p>
                <AnonymusAdsBanner/>
                <h4 className="text-center mt-4 animate-load-from-bottom" onClick={(event) => {
                    window.alert("Opa! Perdão por isso, mas nosso site ainda está em construção e não fizemos um guia prático de como usar nossa ferramenta, mas não desista de nós, é bem fácil de usar: basta copiar o link do vídeo que você quer descobir as tags no YouTube e colar no nosso caompo de URL. Feito isso, clique no aviãozinho e aguarde um instante. Após isso aparecerá todas as tags daquele vídeo :D")
                }}><Link className="text-decoration-none" to="/#">Não sabe como usar? Clique aqui!</Link></h4>
                <img className="main-image animate-load-from-bottom" src={videoTutorialAmicoImage} alt="Video tutorial"/>
                <p className="animate-load-from-bottom">Bem-vindo(a) ao yTag, o seu destino definitivo para descobrir as tags de qualquer vídeo do YouTube! Estamos empolgados por tê-lo(a) aqui, onde você pode explorar e encontrar facilmente as tags que impulsionam os vídeos de sucesso. Sabemos que criar conteúdo no YouTube é emocionante, mas também pode ser desafiador alcançar um público amplo e obter visibilidade. É aí que entra o SEO (Search Engine Optimization) para o YouTube, uma estratégia crucial para aumentar a visibilidade e o alcance do seu vídeo nos mecanismos de busca.</p>
                <p className="animate-load-from-bottom">As tags são componentes essenciais do SEO para o YouTube. Elas são palavras-chave ou frases curtas que descrevem o conteúdo do seu vídeo e ajudam a categorizá-lo. Ao adicionar as tags certas, você aumenta suas chances de ser encontrado por usuários interessados ​​no seu tópico. E aqui no yTag, você terá acesso às tags mais relevantes e populares usadas por criadores de conteúdo de sucesso.</p>
                <p className="animate-load-from-bottom">Ao analisar as tags utilizadas por criadores bem-sucedidos, você pode aprender com eles e aplicar estratégias semelhantes em seus próprios vídeos. Nossa plataforma torna fácil descobrir as tags usadas nos vídeos mais populares, permitindo que você otimize as tags do seu próprio vídeo para aumentar sua visibilidade nas pesquisas do YouTube.</p>
                <p className="animate-load-from-bottom">Estamos aqui para ajudá-lo(a) a aprimorar sua experiência no YouTube e fornecer as ferramentas necessárias para impulsionar seu canal. Com o poder do SEO para o YouTube, você pode maximizar o potencial do seu conteúdo e alcançar resultados incríveis. Então, não perca mais tempo! Junte-se a nós no yTag e vamos começar a explorar as tags que levarão seu conteúdo a novos patamares. Estamos prontos para transformar suas ideias em sucesso no YouTube!</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">O que são tags em um vídeo?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anúncio</p>
                <AdsterraBanner/>
                <img className="animate-load-from-bottom" src={videoUploadAmicoImage} alt="Upload de vídeo"/>
                <p className="animate-load-from-bottom">As tags no YouTube são palavras-chave ou frases curtas que descrevem o conteúdo de um vídeo. Elas servem como metadados que ajudam a categorizar e identificar o assunto do vídeo. As tags são adicionadas pelos criadores de conteúdo durante o processo de upload e desempenham um papel importante na otimização e descoberta de vídeos.</p>
                <p className="animate-load-from-bottom">Ao adicionar tags relevantes ao seu vídeo, você facilita a pesquisa e a localização do seu conteúdo por parte dos usuários. As tags podem abranger termos gerais relacionados ao tópico principal do vídeo, bem como palavras-chave específicas que descrevem detalhes importantes. Ao escolher tags adequadas e precisas, você aumenta as chances de seu vídeo ser encontrado por usuários interessados no seu conteúdo.</p>
                <p className="animate-load-from-bottom">No entanto, é importante equilibrar a relevância e a popularidade das tags. As tags relevantes devem estar diretamente relacionadas ao conteúdo do seu vídeo, enquanto as tags populares são aquelas que são frequentemente pesquisadas pelos usuários. Encontrar a combinação certa de tags relevantes e populares pode aumentar a visibilidade do seu vídeo e atrair mais espectadores.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">O que é SEO?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anúncio</p>
                <AdsterraBanner/>
                <img className="animate-load-from-bottom" src={seoAnalyticsTeamAmicoImage} alt="Equipe de análise de SEO"/>
                <p className="animate-load-from-bottom">SEO (Search Engine Optimization), ou otimização para mecanismos de busca, é uma estratégia utilizada para melhorar a visibilidade e a classificação de um conteúdo nos resultados dos mecanismos de busca, como o YouTube. O objetivo do SEO é atrair tráfego orgânico e direcionar usuários interessados para o seu conteúdo.</p>
                <p className="animate-load-from-bottom">No contexto do YouTube, o SEO envolve a otimização de diferentes elementos do seu vídeo, incluindo as tags, a descrição, o título, as miniaturas e as legendas. Ao otimizar esses elementos, você aumenta suas chances de ser encontrado e recomendado pelo algoritmo do YouTube.</p>
                <p className="animate-load-from-bottom">Além das tags, o SEO também leva em consideração fatores como a qualidade do conteúdo, o engajamento do público, a retenção de espectadores, o número de visualizações e curtidas, entre outros. A otimização adequada do seu vídeo e do seu canal pode levar a um aumento no tráfego, nas visualizações e no crescimento do público.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">Como o SEO afeta meus vídeos?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anúncio</p>
                <AnonymusAdsBanner/>
                <img className="animate-load-from-bottom" src={webSearchAmicoImage} alt="Pesquisa web"/>
                <p className="animate-load-from-bottom">O SEO desempenha um papel significativo no desempenho dos seus vídeos no YouTube. Ao aplicar estratégias de SEO eficazes, você pode afetar positivamente a visibilidade e o alcance do seu conteúdo, influenciando diretamente o número de visualizações e o engajamento dos espectadores.</p>
                <p className="animate-load-from-bottom">Uma boa otimização de SEO, incluindo o uso adequado de tags relevantes e populares, torna mais provável que o algoritmo do YouTube exiba seu vídeo nos resultados de pesquisa relevantes. Isso aumenta a exposição do seu conteúdo a um público mais amplo e direcionado.</p>
                <p className="animate-load-from-bottom">Além disso, um vídeo otimizado para SEO tem mais chances de ser recomendado pelo algoritmo do YouTube em seções como "Vídeos em destaque" e "Vídeos relacionados". Essas recomendações podem gerar um aumento significativo no número de visualizações e, potencialmente, atrair mais inscritos para o seu canal.</p>
                <p className="animate-load-from-bottom">No entanto, é importante lembrar que o SEO é apenas uma parte do sucesso no YouTube. A qualidade do conteúdo, a consistência na publicação de vídeos, a interação com o público e a criação de relacionamentos com outros criadores também são aspectos essenciais para o crescimento do seu canal. Ao combinar uma estratégia sólida de SEO com conteúdo valioso e autêntico, você pode maximizar o potencial de sucesso dos seus vídeos no YouTube.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">Extra!</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anúncio</p>
                <AdCashBanner/>
                <p className="animate-load-from-bottom">Se você tiver interesse em aprofundar seus conhecimentos sobre SEO, recomendamos explorar as seguintes fontes de aprendizado:</p>
                <ul className="animate-load-from-bottom">
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3D6UwfT">SEO 2023: Aprenda otimização de mecanismo de pesquisa com estratégias inteligentes de marketing na Internet</Link></p>
                        <p>Este livro aborda estratégias atualizadas de SEO e fornece orientações práticas para melhorar a classificação nos mecanismos de busca, otimizar o conteúdo e aumentar a visibilidade online.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3O4Yo7k">A Arte do SEO: Dominando a Otimização para Mecanismos de Busca</Link></p>
                        <p>Considerado um dos principais guias sobre SEO, este livro abrange desde os conceitos básicos até as estratégias avançadas, oferecendo uma visão abrangente sobre a otimização para mecanismos de busca.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/43o97hA">Workbook de SEO: Os Sete Passos para o Sucesso da Otimização para Mecanismos de Busca no Google</Link></p>
                        <p>Este livro apresenta um formato de workbook, onde você pode realizar exercícios práticos para aprimorar suas habilidades em SEO. Ele abrange os fundamentos do SEO, análise de concorrência, palavras-chave, link building e muito mais.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3pzIZmh">Otimização para Mecanismos de Busca para Leigos - Tudo em Um</Link></p>
                        <p>Um guia abrangente que aborda diversos aspectos do SEO, incluindo palavras-chave, otimização de conteúdo, link building, análise de dados e estratégias para obter melhores resultados nos mecanismos de busca.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3ro36nZ">SEO como se eu Tivesse 5 Anos: O Guia Definitivo para Iniciantes em Otimização para Mecanismos de Busca</Link></p>
                        <p>Um livro voltado para iniciantes que descomplica os conceitos de SEO e oferece orientações práticas para melhorar a visibilidade do seu site nos mecanismos de busca.</p>
                    </li>
                </ul>
                <p className="animate-load-from-bottom">Lembre-se de que a indústria de SEO está em constante evolução, portanto, ao ler esses livros, certifique-se de complementar seu conhecimento com pesquisas atualizadas e práticas recomendadas mais recentes.</p>
                <button id="btnScrollToTop" title="Voltar ao topo" onClick={scrollToTop}>
                    <i className="bi bi-chevron-up"></i>
                </button>
            </Container>
            <Footer/>
        </>
    )
}

export default Home